.hero {
    height: 100vh;
    width: 100%;
    background-image: url('https://digitalassets.tesla.com/tesla-contents/image/upload/f_auto,q_auto/Homepage-Model-S-Desktop-LHD-6.22.jpg');
    /* Placeholder image */
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
}

.content h1 {
    font-size: 3.5rem;
    margin-bottom: 0.5rem;
    font-weight: 600;
}

.content p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
}

.buttons {
    display: flex;
    gap: 1.5rem;
}

.primaryButton,
.secondaryButton {
    padding: 0.8rem 3rem;
    border-radius: 4px;
    font-size: 0.9rem;
    font-weight: 500;
    transition: all 0.3s ease;
}

.primaryButton {
    background-color: var(--tesla-light);
    color: var(--tesla-dark);
}

.secondaryButton {
    background-color: rgba(0, 0, 0, 0.6);
    color: white;
    backdrop-filter: blur(5px);
}

.primaryButton:hover {
    background-color: white;
}

.secondaryButton:hover {
    background-color: rgba(0, 0, 0, 0.8);
}